import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// react components for routing our app without refresh
import { Link } from "gatsby";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons
// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Button from "components/CustomButtons/Button.jsx";
import Parallax from "components/Parallax/Parallax.jsx";
// sections for this page
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import SectionBasics from "./Sections/SectionBasics.jsx";
import SectionNavbars from "./Sections/SectionNavbars.jsx";
import SectionTabs from "./Sections/SectionTabs.jsx";
import SectionPills from "./Sections/SectionPills.jsx";
import SectionNotifications from "./Sections/SectionNotifications.jsx";
import SectionTypography from "./Sections/SectionTypography.jsx";
import SectionJavascript from "./Sections/SectionJavascript.jsx";
import SectionCarousel from "./Sections/SectionCarousel.jsx";
import SectionCompletedExamples from "./Sections/SectionCompletedExamples.jsx";
import SectionLogin from "./Sections/SectionLogin.jsx";
import SectionExamples from "./Sections/SectionExamples.jsx";
import SectionDownload from "./Sections/SectionDownload.jsx";
import Helmet from 'react-helmet'
import componentsStyle from "assets/jss/material-kit-react/views/components.jsx";

class Components extends React.Component {
  render() {
    const { classes, ...rest } = this.props;
    return (
      <div>
        <Helmet>
          <title>Shri Vitthal Hindu Mandir - Mississauga Hindu Temple &amp; Community Center</title>
          <link rel="pingback" href="https://shrivitthal.com/xmlrpc.php"/>
          <meta name="description" content="SVHM Hindu Mandir is an innovative Hindu Temple located in Mississauga and near Brampton and Toronto. We are more than just a hindu temple - see why."/>
          <link rel="canonical" href="https://shrivitthal.com/" />
          <meta property="og:locale" content="en_CA" />
          <meta property="og:type" content="website" />
          <meta property="og:title" content="Shri Vitthal Hindu Mandir - Mississauga Hindu Temple &amp; Community Center" />
          <meta property="og:description" content="SVHM Hindu Mandir is an innovative Hindu Temple located in Mississauga and near Brampton and Toronto. We are more than just a hindu temple - see why." />
          <meta property="og:url" content="https://shrivitthal.com/" />
          <meta property="og:site_name" content="Shri Vitthal Hindu Mandir" />
          <meta name="twitter:card" content="summary"/>
          <meta name="twitter:description" content="SVHM Hindu Mandir is an innovative Hindu Temple located in Mississauga and near Brampton and Toronto. We are more than just a hindu temple - see why."/>
          <meta name="twitter:title" content="Shri Vitthal Hindu Mandir - Mississauga Hindu Temple &amp; Community Center"/>
          <meta name="twitter:site" content="@shri_vitthal"/>
          <meta name="twitter:domain" content="Shri Vitthal Hindu Mandir"/>
          <meta name="twitter:creator" content="@shri_vitthal"/>
          <meta name="google-site-verification" content="NXh7hUNg6XcXQcKdo39rMCL6VwJEcuqBYGyivAl4W1o" />
        </Helmet>
        <Header
          brand="Shri Vitthal Hindu Mandir"
          rightLinks={<HeaderLinks />}
          fixed
          color="transparent"
          changeColorOnScroll={{
            height: 400,
            color: "white"
          }}
          {...rest}
        />
        <Parallax image={require("assets/img/bg4.jpg")}>
          <div className={classes.container}>
            <GridContainer>
              <GridItem>
                <div className={classes.brand}>
                  <h1 className={classes.title}>Mississauga Hindu Temple</h1>
                  <h3 className={classes.subtitle}>
                    Serving The Greater Toronto Area Since 1996
                  </h3>
                </div>
              </GridItem>
            </GridContainer>
          </div>
        </Parallax>

        <div className={classNames(classes.main, classes.mainRaised)}>
          <SectionBasics />
          <SectionNavbars />
          <SectionTabs />
          <SectionPills />
          <SectionNotifications />
          <SectionTypography />
          <SectionJavascript />
          <SectionCarousel />
          <SectionCompletedExamples />
          <SectionLogin />
          <GridItem md={12} className={classes.textCenter}>
            <Link to={"/login-page"} className={classes.link}>
              <Button color="primary" size="lg" simple>
                View Login Page
              </Button>
            </Link>
          </GridItem>
          <SectionExamples />
          <SectionDownload />
        </div>
        <Footer />
      </div>
    );
  }
}

export default withStyles(componentsStyle)(Components);
